.button {
  padding: 12px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 12px;

  text-transform: capitalize;
  width: 100%;
  background: #08D899;
  box-shadow: 0px 8px 16px rgba(8, 216, 153, 0.2);
  border-radius: 8px;
  @media (min-width: 577px)  {
    font-size: 18px;
    line-height: 24px;  
  }
  img {
    margin-right: 4px;
  }
}